import page_header from "@/components/basic_components/page_header/page_header.vue";
import axios from 'axios';

export default {
	components: {
		page_header,
	},
	data() {
		return {
			contact_platforms: [
				{
					title: 'Call me',
					description: 'Calling will be the most efficient manner of getting in contact with me.',
					image: '/images/undraw/calling.svg',
					link_text: '+316 2805 3633',
					link_icon: '/images/icons/telephone.svg',
					link_href: 'tel:+31628053633',
				},
				{
					title: 'Send an e-mail',
					description: 'Sending an e-mail is always effective. However, it may take a few days for me to respond.',
					image: '/images/undraw/emails.svg',
					link_text: 'rickrosendaal@hotmail.com',
					link_icon: '/images/icons/mail.svg',
					link_href: 'mailto: rickrosendaal@hotmail.com',
				},
				{
					title: 'Add me on LinkedIn',
					description: 'You can view my LinkedIn profile for more information, or add me on LinkedIn to chat.',
					image: '/images/undraw/mobile_messages.svg',
					link_text: 'linkedin.com/in/rickrosendaal',
					link_icon: '/images/icons/telephone.svg',
					link_href: 'https://linkedin.com/in/rickrosendaal',
				},
			],
		};
	},
	methods: {
		async recaptcha(event) {
			const contact_form = document.getElementById('contact-form');
			if (!contact_form.checkValidity()) {
				return;
			}
			event.preventDefault();
			console.log('recaptcha');
			// (optional) Wait until recaptcha has been loaded.
			await this.$recaptchaLoaded();
			console.log('recaptcha v2');


			console.log('show da badgeee v2');

			// Execute reCAPTCHA with action "login".
			const token = await this.$recaptcha('login');
			console.log('token', token);

			try {
				axios.post('https://www.google.com/recaptcha/api/siteverify', {
					secret: '6LeshsEZAAAAABnlVYGHVK2rTt3iesZaTjGS8k16',
					response: token,
				})
					.then(data => {
						console.log(data);
						console.log(data.data);
						//handle the data
					})
					.catch(error => {
						console.log(error.response)
					})
			} catch (e) {
				console.log(e);
			}
			console.log('creating formdata');
			const formData = new FormData(contact_form);
			formData.append('token', token);
			console.log('axiossing');
			const result = await axios.post('https://formsubmit.co/ajax/rickrosendaal@hotmail.com', formData, {
				headers: {
					accept: 'application/json',
				},
			});
			console.log('axiossed');
			console.log(result);
			if (result.data.success) {
				document.getElementById('contact-form-submit-button').innerText = 'Submitted';
			}
			// url: "https://formsubmit.co/ajax/your@email.com",
			// 	method: "POST",
			// 	data: {
			// 	name: "FormSubmit",
			// 		message: "I'm from Devro LABS"
			// },
			// dataType: "json"


			return false;
			// Do stuff with the received token.
		}
	},
}
